import mongoose from "mongoose";

const userSchema = new mongoose.Schema(
  {
     role: {
      type: String,
      enum: ["teacher", "student"], // Teacher or Student
      required: true,
    },
    name: {
      type: String,
      required: true,
      trim: true,
    },
     username: {
      type: String,
      required: true,
      unique: true,
      trim: true,
    },
    email: {
      type: String,
      required: true,
      unique: true,
      lowercase: true,
      trim: true,
    },
    password: {
      type: String,
      required: true,
    },
   
    forgotPasswordToken: String,
    forgotPasswordTokenExpiry: Date,
    verifyToken: String,
    verifyTokenExpiry: Date,
  },
  { timestamps: true }
);

const User = mongoose.models.users || mongoose.model("users", userSchema);

export default User;